:root {

  /* colors */
  --color-1: #4D5061; /* D8DBE2 */
  --color-2: #F6F0ED; /* 4D5061 */
  --color-3: #C2948A; /* A9BCD0 */
  --color-4: #30323D; /* 30323D */
  --color-5: #7EA8BE; /* 58A4B0 */
  --color-6: #E59A89;

  /* layout */
  --size-0: 4px;
  --size-1: 4px;
  --size-2: 8px;
  --size-3: 12px;
  --size-4: 16px;
  --size-5: 24px;
  --size-6: 32px;
  --size-7: 48px;
}

body {
  margin: 0;
  padding: 0;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #4D5061;
  background-color: var(--color-1);
  color: #F6F0ED;
  color: var(--color-2);
}

.row {
  margin-right: 0;
  margin-left: 0;
}

.col, .col-4 {
  padding-right: 0;
  padding-left: 0;
}



.editor-tools-panel {
  display: flex;
}
.control-panel-container {
    border-bottom: 1px solid var(--color-2);
}

.control-panel-item {
    color: var(--color-2);
    padding-top: var(--size-4);
    min-width: 215px;
}

.control-panel-item .header {
    font-size: var(--size-5);
    padding: 0 var(--size-2);
}

.control-panel-item .body {
    padding: 0 var(--size-4);
}

.control-panel-item .btn {
    margin-right: var(--size-4);
    background-color: var(--color-5);
    border-color: var(--color-5);
    color: var(--color-2); 
    width: var(--size-7);
    height: var(--size-7);
}

.tempo, .time-signature {
    margin-bottom: 0;
}

.custom-range {
    border: none;
}

.custom-range:focus, .custom-range:disabled {
    background-color: transparent;
    border: none;
    box-shadow: none;
}
.sequencer-container {
    padding-top: var(--size-4);
}

.sequencer-container .title {
    font-size: var(--size-5);
    padding: 0 var(--size-2);
}

.sequencer-container .body {
    width: auto;
    padding: var(--size-2) var(--size-4);
}

.sequencer-roll {
    color: var(--color-2);
    overflow-x: auto; 
    overflow-y: visible;
    margin-left: 30px;
}

.sequencer-roll-cell {
    width: 30px;
    min-width: 30px;
    max-width: 30px;
    height: 30px;
    min-height: 30px;
    max-height: 30px;
    text-align: center;
    border: 1px solid var(--color-2);
}

.note-value-header {
    position: absolute;
    background-color: var(--color-1);
    left: var(--size-4);
    border: none;
}

.sequencer-roll .bar-header-item {
    position: relative;
}

.sequencer-roll .remove-icon {
    display: inline-block;
    position: absolute;
    right: 4px;
    top: 4px;
}

.sequencer-roll .bar-header-item:hover .remove-icon:hover {
    cursor: pointer;
}

.sequencer-roll .bar-header-item:hover {
    cursor: default; 
}

.sequencer-roll .active-note {
    background-color: var(--color-3);
    cursor: default;
}

.sequencer-roll .beat-header {
    font-size: 12px;
    font-weight: normal;
}

.add-bar-item {
    padding-bottom: var(--size-3);
}

.add-bar-item .btn {
    background-color: var(--color-5);
    border-color: var(--color-5);
    color: var(--color-2);
    margin-right: var(--size-4);
}
.app-header {
  font-size: var(--size-5);
  background-color: var(--color-4);
  padding: var(--size-5);
}

.app-header .col {
  text-align: center;
}

.app-header-title {
  color: var(--color-2);
}

.app-header-title:hover {
  color: var(--color-2);
  text-decoration: none;
}
